import { Component, OnInit, HostListener } from '@angular/core';
import { Paddle } from './shared/model/paddle.model';
import { Ball } from './shared/model/ball.model';
import { Brick } from './shared/model/brick.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  canvas: any;
  canvasCtx: any;
  interVal: any;

  //ball
  ball: Ball = new Ball();

  //paddle
  paddle: Paddle = new Paddle();


  //bricks
  brick: Brick = new Brick();

  bricks = [];

  //score
  score = 0;

  //lives
  lives = 3;

  resetGame() {
    this.ball = new Ball();
    this.paddle = new Paddle();
    this.brick = new Brick();

    this.score = 0;
    this.lives = 3;

    var bricks = [];
    for (var c = 0; c < this.brick.columnCount; c++) {
      bricks[c] = [];
      for (var r = 0; r < this.brick.rowCount; r++) {
        bricks[c][r] = { x: 0, y: 0, status: 1 };
      }
    }
    this.bricks = bricks;
  }

  prepareGameViewPort() {
    this.canvas = document.getElementById("myCanvas");
    this.canvasCtx = this.canvas.getContext("2d");
    this.ball.x = this.canvas.width / 2;
    this.ball.y = this.canvas.height - 30;
    this.paddle.x = (this.canvas.width - this.paddle.width) / 2;

  }

  registerDocEvents() {
    // document.addEventListener("keydown", this.keyDownHandler.bind(this), false);

    
    // this.keyDownHandler;



    // document.addEventListener("keyup", this.keyUpHandler.bind(this), false);
    //document.addEventListener("mousemove", this.mouseMoveHandler.bind(this), false);
  }

  renderGame(){
    this.resetGame();
    this.prepareGameViewPort();
    this.draw();
  }

  ngOnInit(): void {
    this.registerDocEvents();
    this.renderGame();
  }

  title = 'breakup';

  mouseMoveHandler(e) {
    var relativeX = e.clientX - this.canvas.offsetLeft;
    if (relativeX > 0 && relativeX < this.canvas.width) {
      this.paddle.x = relativeX - this.paddle.width / 2;
    }
  }
  animating = false;
  canvasClickHandler() {
    if (this.animating) {
      this.stopAnnimation();
    } else {
      this.playAnimation();
    }
    this.animating = !this.animating;
  }
  canvasDoubleClkHandler() {
    this.playAnimation();
  }
  playAnimation() {
    this.interVal = setInterval(this.draw.bind(this), 10);
  }
  stopAnnimation() {
    if (this.interVal) clearInterval(this.interVal);
  }

  draw() {

    this.canvasCtx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.drawBricks();
    this.drawBall();
    this.ball.x += this.ball.dx;
    this.ball.y += this.ball.dy;

    if (this.ball.x + this.ball.dx > this.canvas.width - this.ball.radius || this.ball.x + this.ball.dx < this.ball.radius) {
      this.ball.dx = -this.ball.dx;
    }

    if (this.ball.y + this.ball.dy < this.ball.radius) {
      this.ball.dy = -this.ball.dy;
    } else if (this.ball.y + this.ball.dy > this.canvas.height - this.ball.radius) {
      if (this.ball.x > this.paddle.x && this.ball.x < this.paddle.x + this.paddle.width) {
        this.ball.dy = -this.ball.dy;
      }
      else {
        this.lives--;
        if (!this.lives) {
          this.stopAnnimation();
          this.renderGame();
        }
        else {
          this.ball.dx = this.canvas.width / 2;
          this.ball.y = this.canvas.height - 30;
          this.ball.dx = 2;
          this.ball.dy = -2;
          this.paddle.x = (this.canvas.width - this.paddle.width) / 2;
        }
      }
    }

    this.drawPaddle();
    this.drawScore();
    this.drawLives();
    this.drawInstruction();
    this.collisionDetection();

    if (this.paddle.rightPressed && this.paddle.x < this.canvas.width - this.paddle.width) {
      this.paddle.x += 7;
    } else if (this.paddle.leftPressed && this.paddle.x > 0) {
      this.paddle.x -= 7;
    }

    this

  }

  drawBall() {

    this.canvasCtx.beginPath();
    console.count("drawing");

    this.canvasCtx.arc(this.ball.x, this.ball.y, this.ball.radius, 0, Math.PI * 2);
    this.canvasCtx.fillStyle = "#0095DD";
    this.canvasCtx.fill();
    this.canvasCtx.closePath();

  }

  drawPaddle() {
    this.canvasCtx.beginPath();
    this.canvasCtx.rect(this.paddle.x, this.canvas.height - this.paddle.height, this.paddle.width, this.paddle.height);
    this.canvasCtx.fillStyle = "#0095DD";
    this.canvasCtx.fill();
    this.canvasCtx.closePath();
  }

  drawBricks() {
    var bricks = this.bricks;
    var ctx = this.canvasCtx;
    for (var c = 0; c < this.brick.columnCount; c++) {
      for (var r = 0; r < this.brick.rowCount; r++) {
        if (bricks[c][r].status == 1) {

          var brickX = (c * (this.brick.width + this.brick.padding)) + this.brick.offsetLeft;
          var brickY = (r * (this.brick.height + this.brick.padding)) + this.brick.offsetTop;
          bricks[c][r].x = brickX;
          bricks[c][r].y = brickY;

          ctx.beginPath();
          ctx.rect(brickX, brickY, this.brick.width, this.brick.height);
          ctx.fillStyle = "#0095DD";
          ctx.fill();
          ctx.closePath();
        }
      }
    }
  }

  collisionDetection() {
    for (var c = 0; c < this.brick.columnCount; c++) {
      for (var r = 0; r < this.brick.rowCount; r++) {
        var b = this.bricks[c][r];
        if (b.status == 1) {
          if (this.ball.x > b.x && this.ball.x < b.x + this.brick.width && this.ball.y > b.y && this.ball.y < b.y + this.brick.height) {
            this.ball.dy = -this.ball.dy;
            b.status = 0;
            this.score++;

            if (this.score == this.brick.rowCount * this.brick.columnCount) {
              alert("YOU WIN, CONGRATULATIONS!");
              document.location.reload();
              this.stopAnnimation();
            }
          }
        }
      }
    }
  }

  drawScore() {
    var ctx = this.canvasCtx;
    ctx.font = "16px Arial";
    ctx.fillStyle = "#0095DD";
    ctx.fillText("Score: " + this.score, 8, 20);
  }

  drawInstruction() {
    var ctx = this.canvasCtx;
    ctx.font = "16px Arial";
    ctx.fillStyle = "#291111";
    ctx.fillText("tap to start or pause.", 100, 20);
  }
  drawLives() {
    var ctx = this.canvasCtx;
    ctx.font = "16px Arial";
    ctx.fillStyle = "#0095DD";
    ctx.fillText("Lives: " + this.lives, this.canvas.width - 65, 20);
  }

  tapDownHandler(key: string) {
console.count('tapdown');
    if (key === 'R') {
      this.paddle.rightPressed = true;
      
      if (this.paddle.rightPressed && this.paddle.x < this.canvas.width - this.paddle.width) {
        this.paddle.x += this.paddle.width;
      } 
    }
    else if (key == 'L') {
      this.paddle.leftPressed = true;
      

      if (this.paddle.leftPressed && this.paddle.x > 0) {
        this.paddle.x -= this.paddle.width;
      }
    }
  }

  tapUpHandler(key: string) {
    console.count('tapup');
    if (key === 'R') {
      this.paddle.rightPressed = false;
    }
    else if (key == 'L') {
      this.paddle.leftPressed = false;
    }
  }

  @HostListener('window:keydown', ['$event'])
  keyDownHandler(e) {
    if (e.key == "Right" || e.key == "ArrowRight") {
      this.paddle.rightPressed = true;
    }
    else if (e.key == "Left" || e.key == "ArrowLeft") {
      this.paddle.leftPressed = true;
    }
  }

  @HostListener('window:keyup', ['$event'])
  keyUpHandler(e) {
    if (e.key == "Right" || e.key == "ArrowRight") {
      this.paddle.rightPressed = false;
    }
    else if (e.key == "Left" || e.key == "ArrowLeft") {
      this.paddle.leftPressed = false;
    }
  }

}
